.title {
  margin: 20px 0;
  text-align: center;
}

.title a {
  font-size: 30px;
  cursor: pointer;
  position: relative;
  z-index: 2;
  user-select:         none; /* CSS3          */
  -moz-user-select:    none; /* Firefox       */
  -webkit-user-select: none; /* Safari Chrome */
  -ms-user-select:     none; /* IE10以降      */
}

.items {
  margin: 0 auto;
  max-width: 1100px;
}

.items .size-cover {
  background-size: cover;
}

.items .contain {
  background-size: contain;
}

.items div,
.items a {
  position: relative;
  float: left;
  width: 100px;
  height: 100px;
  margin: 0 10px 10px 0;
  background-repeat: no-repeat;
  background-color: #fff;
  border-radius: 20px;
  display: block;
  cursor: pointer;
}

.items [searchTag] {
  color: rgba(0, 0, 0, 0);
}

@media (min-width: 320px) and (max-width: 329px) {
  .items :nth-child(3n+1) {
    margin-right: 0;
  }
}

.items .selected {
  position: relative;
  width: 320px;
  height: 320px;
  cursor: auto;
  background-position: center center;
}

.items .selected .taInfo {
  position: relative;
  cursor: auto;
  display: none;
  width: 320px;
  height: 320px;
  background-color: #fff;
  opacity: .95;
  z-index: 2;
}

.items .selected .taInfo p:first-child {
  margin: 40px 0 30px;
}

.items .selected .taInfo p {
  position: relative;
  text-align: center;
  margin-top: 20px;
  line-height: 25px;
  font-size: 25px;
  color: #000;
}

.items .selected .taInfo p.taReset {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100px;
  height: 25px;
  font-size: 20px;
  margin: 0;
  border-radius: 20px;
  background-color: #ccc;
  cursor: pointer;
}

.items .selected .onlineInfo {
  position: relative;
  cursor: auto;
  display: none;
  width: 320px;
  height: 320px;
  background-color: #fff;
  opacity: .95;
  z-index: 2;
}

.items .selected .onlineInfo p {
  position: relative;
  text-align: center;
  margin-top: 25px;
  line-height: 25px;
  font-size: 25px;
  color: #000;
}

.items .tweet {
  position: relative;
  background-color: #55acee;
  background-image: url(/images/Twitter_logo_white.png);
  background-size: auto;
  background-position: center center;
}

.items p {
  position: absolute;
  text-align: center;
  color: #fff;
  width: 100%;
  bottom: 7px;
}

.items p.rankingCount {
  bottom: 0;
  right: 0;
  width: 40px;
  height: 25px;
  font-size: 18px;
  line-height: 25px;
  margin: 0;
  border-radius: 20px 0 0 0 / 20px 0 0 0;
  color: #000;
  background-color: #eee;
}

.items .count {
  position: relative;
  text-align: center;
  line-height: 100px;
  font-size: 20px;
}

.items .count.over100000 {
  color: white;
}

.items .search-hit-thumbnail {
  background-clip: content-box;
  background-origin: border-box;
  border: solid 3px #ff0000;
  box-sizing: border-box;
}

.items .selected-thumbnail {
  background-clip: content-box;
  background-origin: border-box;
  border: solid 3px #55acee;
  box-sizing: border-box;
}

a.birthday {
  display: none;
  position: relative;
  background-color: #DFEAD6;
}

.birthday p {
  top: 20px;
  line-height: 20px;
  color: #000;
}

a.sta4koma {
  position: relative;
  background-image: url(/images/sta4koma.png);
  background-size: 80%;
  background-position: center center;
}

.sta4koma p {
  color: #000;
}

a.stabook {
  position: relative;
  background-image: url(/images/stabook.png);
  background-position: center center;
}

a.hobo_stabook {
  position: relative;
  background-image: url(/images/hobo_stabook.png);
  background-position: center center;
}

a.furoshiki {
  background-image: url(/images/furoshiki.png);
  background-position: center center;
  position: relative;
}

a.furoshiki p {
  color: #fff;
  font-weight: bold;
  text-shadow:  2px  2px 2px #004D00,
               -2px  2px 2px #004D00,
               -2px -2px 2px #004D00,
                2px -2px 2px #004D00;
}

a.linestamp {
  background-color: #00c300;
  background-image: url(/images/linestamp.png);
  background-position: center center;
  position: relative;
}

a.linestamp p {
  color: #000;
  font-size: 90%;
  font-weight: bold;
  text-shadow:  1px  1px 1px #00c300,
               -1px  1px 1px #00c300,
               -1px -1px 1px #00c300,
                1px -1px 1px #00c300;
}

a.statshirt {
  position: relative;
  background-image: url(/images/statshirt.png);
}

.statshirt p {
  color: #000;
}

.statobashi p {
  color: #000;
  line-height: 20px;
  top: 20px;
  bottom: 0;
}

.reply p {
  color: #000;
  line-height: 20px;
  font-size: 17px;
  top: 30px;
  bottom: 0;
}

.reply textarea {
  background: #fff;
  border: 0;
  display: none;
  font-size: 10px;
  height: 80px;
  line-height: 15px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  resize: none;
  top: 10px;
  width: 80px;
  word-break: break-all;
  word-wrap: break-word;
}

.mode label {
  display: block;
  margin: 22px 0;
  text-align: center;
  line-height: 17px;
  font-size: 17px;
}

.mode2 label:nth-child(1) {
  margin: 22px 0 0;
}

.mode2 label:nth-child(2) {
  margin: 5px 0 0;
}

.modelabel.on {
  color: #ee55ac;
}

.modelabel.off {
  color: #55acee;
}

.sync {
  position: relative;
  text-align: center;
  line-height: 100px;
  font-size: 17px;
}

.search label {
  color: #000;
  line-height: 100px;
}

.popupMessage {
  display: none;
  position: fixed;
  top: 20px;
  left: 20px;
  background-color: #000;
  color: #fff;
  padding: 10px;
  border-radius: 10px;
}

.scrollToTop {
  background-color: #000;
  border-radius: 10px;
  color: #fff;
  display: none;
  font-size: 30px;
  height: 50px;
  line-height: 50px;
  padding: 10px;
  position: fixed;
  right: 20px;
  text-align: center;
  top: 20px;
  width: 50px;
}

.arrow:after {
  border: solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(255, 255, 255, 0);
  border-width: 10px;
}

@media (max-width: 439px) {
  .tweet:after {
    bottom: 100%;
    left: 50%;
    border-bottom-color: #55acee;
    margin-left: -10px;
  }

  .count:after {
    right: 100%;
    top: 50%;
    border-right-color: #fff;
    margin-top: -10px;
  }
}

@media (min-width: 440px) and (max-width: 549px) {
  .tweet:after {
    right: 100%;
    top: 50%;
    border-right-color: #55acee;
    margin-top: -10px;
  }

  .count:after {
    bottom: 100%;
    left: 50%;
    border-bottom-color: #fff;
    margin-left: -10px;
  }
}

@media (min-width: 550px) {
  .tweet:after {
    right: 100%;
    top: 50%;
    border-right-color: #55acee;
    margin-top: -10px;
  }

  .count:after {
    right: 100%;
    top: 50%;
    border-right-color: #fff;
    margin-top: -10px;
  }
}

#animation-canvas-wrapper {
  height: 100%;
  left: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  width: 100%;
}

#animation-canvas {
  pointer-events: none;
}

.clearboth {
  clear: both;
}

.footer {
  text-align: center;
}

.launchCounter {
  position: fixed;
  top: 50px;
  left: 20px;
  z-index: 2;
}

